import { primary, code } from '../../variables/colors';

export default {
  container: {
    height: 450
  },

  tab: {
    marginTop: '3rem'
  },

  paper: {
		wordBreak: 'break-word',
    background: code,
    padding: 20,
    fontSize: 18,
    letterSpacing: 0.5,
  },

  highlight: {
    background: code,
    padding: 5,
		lineHeight: 2,
  },

  para: {
    marginTop: '3rem',
    letterSpacing: 0.2,
    fontSize: 18
  },

  indicatorColor: primary
};



